import { Component ,OnInit} from '@angular/core';
import { NavigationStart, Router } from '@angular/router';
import {
  AuthKeycloakService,
  ConfigurationsService,
  TelemetryService,
  UtilityService,
  ValueService,
  WsEvents,
} from '@ws-widget/utils'
@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent implements OnInit{
  isCommonChatEnabled = true
  showPublicNavbar = true
  showNavbar = true
  isXSmall$ = this.valueSvc.isXSmall$
  constructor(private valueSvc: ValueService,public router:Router, private utilityService: UtilityService) {

  }
  ngOnInit() {
    this.meth();
    this.router.events.subscribe((event:any)=>{
      if(event instanceof NavigationStart){
        if(event.url.includes('/app/create-account')){
          this.showPublicNavbar= false
        }
      }
    })
  }
  async meth() {
    console.log('****************************************')

    const baseUrl = await this.utilityService.getBuildConfigValue('BASE_URL');
    console.log('Checking base url.........', baseUrl)
  }
}
